body {
  background-color: #222B2F;
  font-family: 'Lato', sans-serif;
  font-size: 16px;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  font-family: 'Oswald', sans-serif;
}

h1 {
  font-size: 3.0vw;
}

h2 {
  font-size: 2.2vw;
}

h3 {
  font-size: 2.0vw;
}

h5 {
  font-size: 1.0vw;
}

span {
  margin: 0;
}

.periodic-table {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}

.card-element-wrapper {
  display: block;
  width: 100%;
  height: 100%;
  color: #F8FAFC;
  background-color: #3D4852;
  border-width: 1px 2px 2px 2px;
  border-style: solid;
  border-color: #222B2F;
  text-align: left;
  grid-column: 3 / 13;
  grid-row: 2 / 4;
}

.card-element-container {
  display: block;
  width: 100%;
  height: 100%;
  padding: 5px;
}

.card-element-general {
  display: inline-block;
  color: #222B2F;
  padding: 5px;
  width: 15.8vw;
  text-align: center;
}

.card-element-name {
  text-transform: capitalize;
}

.card-element-properties {
  display: inline-block;
}

.card-element-label,
card-element-boiling-point {
  display: inline-block;
}

.elements-wrapper {
  display: inline-block;
  width: 97.2vw;
  margin: 0 auto;
}

.elements-container {
  display: grid;
  grid-template-columns: 5.4vw 5.4vw 5.4vw 5.4vw 5.4vw 5.4vw 5.4vw 5.4vw 5.4vw 5.4vw 5.4vw 5.4vw 5.4vw 5.4vw 5.4vw 5.4vw 5.4vw 5.4vw;
  grid-template-rows: 5.4vw 5.4vw 5.4vw 5.4vw 5.4vw 5.4vw 5.4vw;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: 100%;
  text-align: center;
}

.element {
  height: 5.4vw;
  width: 5.4vw;
  border: 1px solid #222B2F;
  color: #222B2F;
  background-color: #F8FAFC;
  opacity: 0.9;
  -webkit-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

.element:hover,
.element:focus {
  opacity: 1;
  cursor: pointer;
}

.element:last-child {
  margin-right: 0;
}

.element-container {
  padding: 2px;
}

.atomic-info {
  display: block;
  text-align: left;
  width: 100%;
  height: 1vw;
}

.atomic-number {
  display: inline-block;
  margin: 0px 0px 0px 2px;
  font-size: 0.75vw;
  vertical-align: top;
}

.atomic-mass {
  float: right;
  margin: 0px 2px 0px 0px;
  font-size: 0.75vw;
}

h2.gas {
  -webkit-text-stroke: 2px #222B2F;
  color: transparent;
}

h2.liquid {
  -webkit-text-stroke: 2px #222B2F;
  color: #F8FAFC;
}

.element-info {
  width: 100%;
  text-align: center;
}

.element-symbol {
  display: block;
  width: 100%;
}

.element-name {
  display: block;
  font-size: 0.75vw;
}

.electron-configuration {
  display: block;
  margin: auto;
  text-align: center;
  font-size: 0.5vw;
}

.nonmetal {
  background-color: #6CB2EB;
}

.alkali {
  background-color: #EF5753;
}

.alkaline {
  background-color: #FAAD63;
}

.transition {
  background-color: #FFF382;
}

.post-transition {
  background-color: #51D88A;
}

.metalloid {
  background-color: #64D5CA;
}

.noble {
  background-color: #D6BBFC;
}

.lanthanide {
  background-color: #B2B7FF;
}

.actinide {
  background-color: #FA7EA8;
}

.placeholder {
  cursor: default !important;
}

.placeholder:hover,
.placeholder:focus {
  opacity: 0.9;
}

@media (max-width: 990px) {
  .periodic-table {
    max-width: 990px;
  }

  .period-container {
    display: none;
  }

  .elements-wrapper {
    width: 100%;
  }

  .elements-container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    width: 100%;
  }

  .element {
    display: inline-block;
    height: 105px;
    width: 105px;
  }

  h1 {
    font-size: 50px;
  }

  h2 {
    font-size: 40px;
  }

  h3 {
    font-size: 36px;
  }

  h5 {
    font-size: 20px;
  }

  .card-element-general {
    width: auto;
  }

  .atomic-info {
    height: auto;
  }

  .atomic-number {
    font-size: 14px;
  }

  .atomic-mass {
    font-size: 14px;
  }

  .element-name {
    font-size: 14px;
  }

  .electron-configuration {
    font-size: 10px;
  }
}

@media (max-width: 475px) {
  .card-element-general {
    width: 100%;
  }
}